$x-color: rgb(207, 37, 37);
$o-color: rgb(90, 183, 56);
$base-color: royalblue;
.user-panel {
  max-width: 600px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
}

.score-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /*   max-width: 8rem; */
}

.first-user-panel,
.second-user-panel {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.first-user-panel > *,
.second-user-panel > * {
  font-weight: 600;
  font-size: 1.5rem;
}

.first-user-panel {
  color: $x-color;
}

.first-user-name {
  padding: 1rem;
  text-decoration: underline;
  font-size: 2rem;
  box-shadow: 5px 5px 5px rgb(151, 26, 26);
  border: 2px solid $x-color;
  background-color: $x-color;
  color: white;
  border-radius: 0.5rem;
  width: 3rem;
  margin: 0;
}

.second-user-panel {
  color: $o-color;
}

.second-user-name {
  padding: 1rem;
  text-decoration: underline;
  font-size: 2rem;
  box-shadow: 5px 5px 5px rgb(73, 151, 45);
  background-color: $o-color;
  color: white;
  border: 2px solid $o-color;
  border-radius: 0.5rem;
  width: 3rem;
  margin: 0;
}

.inactive {
  background-color: transparent;
  color: inherit;
  text-decoration: none;
  transform: scale(0.8);
  transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}

@media (max-width: 450px) {
  .first-user-panel > *,
  .second-user-panel > * {
    font-size: 1rem;
  }
  .first-user-name,
  .second-user-name {
    padding: 0.6rem;
    font-size: 1.5rem;
    width: 2rem;
  }
}
